<template>
    <div>
        <!--  -->
        <el-card>
            <!--  -->
            <el-row class="search-header" :gutter="20">
                <el-col :span="8">
                    <el-input placeholder="" v-model="queryParams.query" clearable
                              @clear="getDirectorScreenwriterList">
                        <el-button slot="append" icon="el-icon-search" @click="getDirectorScreenwriterBySearch"></el-button>
                    </el-input>
                </el-col>
                <el-col :span="4">
                    <el-button type="primary" @click="addDialogVisible = true"></el-button>
                </el-col>
            </el-row>
            <!--  -->
            <el-table :data="directorScreenwriterList" border stripe>
                <el-table-column label="id" prop="id" sortable></el-table-column>
                <el-table-column label="" prop="name"></el-table-column>
                <el-table-column label="" prop="foreign_name"></el-table-column>
                <el-table-column label="" prop="cover_url">
                    <template slot-scope="scope">
                        <el-image :src="scope.row.cover_url"></el-image>
                    </template>
                </el-table-column>

                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-switch
                                v-model="scope.row.isDirector"
                                active-color="#36c6d3">
                            <!--@change="userStateChanged($event,scope.row)">-->
                        </el-switch>
                    </template>
                </el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-switch
                                v-model="scope.row.isScreenwriter"
                                active-color="#36c6d3">
                            <!--@change="userStateChanged($event,scope.row)">-->
                        </el-switch>
                    </template>
                </el-table-column>
                <el-table-column label="" width="200px">
                    <template slot-scope="scope">
                        <!--  -->
                        <el-button type="primary" icon="el-icon-edit" size="mini"
                                   @click="showEditDialog(scope.row.id)"></el-button>
                        <!--  -->
                        <el-button type="danger" icon="el-icon-delete" size="mini"
                                   @click="deleteDirectorScreenwriterById(scope.row.id)"></el-button>
                    </template>
                </el-table-column>
            </el-table>

            <!--  -->
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
                           :current-page="queryParams.pageNum" :page-sizes="[1, 2, 5, 10]"
                           :page-size="queryParams.pageSize"
                           layout="total, sizes, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </el-card>

        <!--  -->
        <el-dialog title="" :visible.sync="addDialogVisible" width="50%" @close="addObjDialogClosed">
            <!--  -->
            <el-form :model="addForm" ref="addFormRef" label-width="150px">
                <el-form-item label="" prop="name">
                    <el-input v-model="addForm.name"></el-input>
                </el-form-item>
                <el-form-item label="" prop="foreign_name">
                    <el-input v-model="addForm.foreign_name"></el-input>
                </el-form-item>
                <el-form-item label="" prop="cover_url">
                    <el-input v-model="addForm.cover_url"></el-input>
                </el-form-item>
                <el-form-item label="" prop="isDirector">
                    <el-switch
                            v-model="addForm.isDirector"
                            active-color="#36c6d3">
                        <!--@change="userStateChanged($event,scope.row)">-->
                    </el-switch>
                </el-form-item>
                <el-form-item label="" prop="isScreenwriter">
                    <el-switch
                            v-model="addForm.isScreenwriter"
                            active-color="#36c6d3">
                        <!--@change="userStateChanged($event,scope.row)">-->
                    </el-switch>
                </el-form-item>
                <el-form-item label="" prop="douban_link">
                    <el-input v-model="addForm.douban_link"></el-input>
                </el-form-item>
                <el-form-item label="imdb" prop="imdb_link">
                    <el-input v-model="addForm.imdb_link"></el-input>
                </el-form-item>
            </el-form>
            <!--  -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="addDialogVisible = false"> </el-button>
                <el-button type="primary" @click="addDirectorScreenwriter"> </el-button>
            </span>
        </el-dialog>

        <!--  -->
        <el-dialog title="" :visible.sync="editDialogVisible" width="30%"
                   @close="editDirectorScreenwriterDialogClosed">
            <el-form :model="editForm" ref="editFormRef" label-width="70px">
                <el-form-item label="id">
                    <el-input v-model="editForm.id" disabled></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="editForm.name"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="editForm.foreign_name"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="editForm.cover_url"></el-input>
                </el-form-item>
                <el-form-item label="?">
                    <el-switch
                            v-model="editForm.isDirector"
                            active-color="#36c6d3">
                        <!--@change="userStateChanged($event,scope.row)">-->
                    </el-switch>
                </el-form-item>
                <el-form-item label="?">
                    <el-switch
                            v-model="editForm.isScreenwriter"
                            active-color="#36c6d3">
                        <!--@change="userStateChanged($event,scope.row)">-->
                    </el-switch>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="editForm.douban_link"></el-input>
                </el-form-item>
                <el-form-item label="imdb">
                    <el-input v-model="editForm.imdb_link"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                 <el-button @click="editDialogVisible = false"> </el-button>
                 <el-button type="primary" @click="editDirectorScreenwriter"> </el-button>
            </span>
        </el-dialog>


    </div>
</template>

<script>

    export default {
        name: "AdminDirectorScreenwriter",
        data() {

            return {
                // 
                queryParams: {
                    query: '',
                    // 
                    pageNum: 1,
                    // 
                    pageSize: 2
                },
                // 
                total: 0,
                // 
                directorScreenwriterList: [],

                // 
                addDialogVisible: false,
                // 
                addForm: {
                    name: '',
                    foreign_name: '',
                    cover_url: '',
                    isDirector: true,
                    isScreenwriter: false,
                    douban_link: '',
                    imdb_link: ''
                },
                // 
                editDialogVisible: false,
                // 
                editForm: {},

            };
        },
        methods: {

            async getDirectorScreenwriterBySearch() {
                if (this.queryParams.query === '') {
                    return this.directorScreenwriterList.length > 1 ? this.$message.warning('') : this.getDirectorScreenwriterList();
                }
                const {data: result} = await this.$http.get(
                    'directorScreenwriter/_name/' + this.queryParams.query,
                );
                if (result.status === 200) {
                    this.directorScreenwriterList = [];
                    this.directorScreenwriterList.push(result.data);
                    this.total = 1;
                } else {
                    this.directorScreenwriterList = [];
                    this.total = 0;
                    return this.$message.error(result.message)
                }
            },

            // 
            async getDirectorScreenwriterList() {
                const {data: result} = await this.$http.get(
                    'directorScreenwriter/_all',
                    {
                        params: {
                            pageNum: this.queryParams.pageNum,
                            pageSize: this.queryParams.pageSize
                        }
                    });
                if (result.status !== 200) {
                    return this.$message.error(result.message)
                }
                console.log(result.data.content);
                this.directorScreenwriterList = result.data.content;
                this.total = result.data.totalElements;
            },
            // 
            addDirectorScreenwriter() {
                this.$refs.addFormRef.validate(async valid => {
                    if (!valid) return;
                    // 
                    console.log("admin  ======>");
                    console.log(this.addForm);
                    const {data: result} = await this.$http.post('directorScreenwriter', this.addForm);

                    if (result.status !== 200) {
                        this.$message.error(result.message);
                    }
                    this.$message.success(result.message);
                    console.log("admin  ");
                    // 
                    this.addDialogVisible = false;
                    // 
                    this.getDirectorScreenwriterList();
                })
            },
            // 
            editDirectorScreenwriter() {
                this.$refs.editFormRef.validate(async valid => {
                    if (!valid) return;
                    // 
                    const {data: result} = await this.$http.put(
                        'directorScreenwriter/' + this.editForm.id,
                        this.editForm
                    );

                    if (result.status !== 200) {
                        return this.$message.error(result.message)
                    }

                    // 
                    this.editDialogVisible = false;
                    // 
                    this.getDirectorScreenwriterList();
                    // 
                    this.$message.success(result.message)
                })
            },
            // , 
            async showEditDialog(id) {
                // console.log(id)
                const {data: result} = await this.$http.get('directorScreenwriter/' + id);

                if (result.status !== 200) {
                    return this.$message.error(result.message)
                }

                this.editForm = result.data;
                this.editDialogVisible = true
            },
            // Id
            async deleteDirectorScreenwriterById(id) {
                // 
                const confirmResult = await this.$confirm(
                    ', ?',
                    '',
                    {
                        confirmButtonText: '',
                        cancelButtonText: '',
                        type: 'warning'
                    }
                ).catch(err => err);
                //  confirm
                //  cancel
                // console.log(confirmResult)
                if (confirmResult !== 'confirm') {
                    return this.$message.info('')
                }
                const {data: result} = await this.$http.delete('directorScreenwriter/' + id);

                if (result.status !== 200) {
                    return this.$message.error(result.message);
                }
                this.$message.success(result.message);
                this.getDirectorScreenwriterList()
            },

            // 
            addObjDialogClosed() {
                this.$refs.addFormRef.resetFields();
            },
            // 
            editDirectorScreenwriterDialogClosed() {
                this.$refs.editFormRef.resetFields();
            },
            //  pageSize 
            handleSizeChange(newSize) {
                console.log("pageSize: ", newSize);
                this.queryParams.pageSize = newSize;
                this.getDirectorScreenwriterList();
            },
            //   
            handleCurrentChange(newPage) {
                console.log("pageNum: ", newPage);
                this.queryParams.pageNum = newPage;
                this.getDirectorScreenwriterList();
            }

        },
        created() {
            this.getDirectorScreenwriterList();
        }

    }
</script>

<style lang="scss" scoped>
    @import "src/assets/sass/global";
    .el-card {
        .search-header {
            margin-bottom: 20px;
            ::v-deep .el-button--default:hover{
                background-color: $bg_blue_white_global;
            }
        }
        .el-pagination {
            margin-top: 20px;
        }
    }

</style>