<template>
    <div>
        <!--  -->
        <el-card>
            <!--  -->
            <el-row class="search-header" :gutter="20">
                <el-col :span="8">
                    <el-input placeholder="" v-model="queryParams.query" clearable @clear="getPermissionList">
                        <el-button slot="append" icon="el-icon-search" @click="getPermissionByName"></el-button>
                    </el-input>
                </el-col>
                <el-col :span="4">
                    <el-button type="primary" @click="addDialogVisible = true"></el-button>
                </el-col>
            </el-row>
            <!--  -->
            <el-table :data="permissionList" border stripe>
                <el-table-column label="id" prop="id"></el-table-column>
                <el-table-column label="" prop="name"></el-table-column>
                <el-table-column label="" prop="resource"></el-table-column>
                <el-table-column label="" prop="description"></el-table-column>
                <el-table-column label="">
                    <template>
                        <el-switch
                                active-color="#36c6d3">
                        </el-switch>
                    </template>
                </el-table-column>
                <el-table-column label="" width="200px">
                    <template slot-scope="scope">
                        <!--  -->
                        <el-button type="primary" icon="el-icon-edit" size="mini"
                                   @click="showEditDialog(scope.row.id)"></el-button>
                        <!--  -->
                        <el-button type="danger" icon="el-icon-delete" size="mini"
                                   @click="removePermissionById(scope.row.id)"></el-button>
                    </template>
                </el-table-column>
            </el-table>

            <!--  -->
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
                           :current-page="queryParams.pageNum" :page-sizes="[1, 2, 5, 10]"
                           :page-size="queryParams.pageSize"
                           layout="total, sizes, prev, pager, next, jumper" :total="total">
            </el-pagination>

        </el-card>

        <!--  -->
        <el-dialog title="" :visible.sync="addDialogVisible" width="30%" @close="addObjDialogClosed">
            <!--  -->
            <el-form :model="addForm" ref="addFormRef" label-width="60px">
                <el-form-item label="" prop="name">
                    <el-input v-model="addForm.name"></el-input>
                </el-form-item>
                <el-form-item label="" prop="resource">
                    <el-input v-model="addForm.resource"></el-input>
                </el-form-item>
                <el-form-item label="" prop="description">
                    <el-input v-model="addForm.description"></el-input>
                </el-form-item>

            </el-form>
            <!--  -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="addDialogVisible = false"> </el-button>
                <el-button type="primary" @click="addPermission"> </el-button>
            </span>
        </el-dialog>

        <!--  -->
        <el-dialog title="" :visible.sync="editDialogVisible" width="50%" @close="editObjDialogClosed">
            <el-form :model="editForm" ref="editFormRef" label-width="70px">
                <el-form-item label="id">
                    <el-input v-model="editForm.id" disabled></el-input>
                </el-form-item>
                <el-form-item label="" prop="name">
                    <el-input v-model="editForm.name"></el-input>
                </el-form-item>
                <el-form-item label="" prop="resource">
                    <el-input v-model="editForm.resource"></el-input>
                </el-form-item>
                <el-form-item label="" prop="description">
                    <el-input v-model="editForm.description"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                 <el-button @click="editDialogVisible = false"> </el-button>
                 <el-button type="primary" @click="editPermission"> </el-button>
            </span>
        </el-dialog>


    </div>
</template>

<script>
    export default {
        name: "AdminRole",
        data() {

            return {
                // 
                queryParams: {
                    query: '',
                    // 
                    pageNum: 1,
                    // 
                    pageSize: 2
                },
                // 
                total: 0,
                // 
                permissionList: [],

                // 
                addDialogVisible: false,
                // 
                addForm: {
                    name: '',
                    resource: '',
                    description: ''
                },
                // 
                editDialogVisible: false,
                // 
                editForm: {

                },

            };
        },
        methods:{

            // 
            async getPermissionList() {
                const {data: result} = await this.$http.get(
                    'permission/_all',
                    {
                        params: {
                            pageNum: this.queryParams.pageNum,
                            pageSize: this.queryParams.pageSize
                        }
                    });
                if (result.status !== 200) {
                    return this.$message.error(result.message)
                }
                this.permissionList = result.data.content;
                this.total = result.data.totalElements;
            },

            // 
            async getPermissionByName() {
                if (this.queryParams.query === '') {
                    return this.permissionList.length > 1 ? this.$message.warning('') : this.getPermissionList();
                }
                const {data: result} = await this.$http.get(
                    'permission/_name/' + this.queryParams.query,
                );
                if (result.status === 200) {
                    this.permissionList = [];
                    this.permissionList.push(result.data);
                    this.total = 1;
                } else {
                    this.permissionList = [];
                    this.total = 0;
                    return this.$message.error(result.message)
                }
            },

            // 
            addPermission() {
                this.$refs.addFormRef.validate(async valid => {
                    if (!valid) return;
                    // 
                    console.log("admin  ======>");
                    console.log(this.addForm);
                    const {data: result} = await this.$http.post('permission', this.addForm);

                    if (result.status !== 200) {
                        this.$message.error(result.message);
                    }
                    this.$message.success(result.message);
                    console.log("admin  ");
                    // 
                    this.addDialogVisible = false;
                    // 
                    this.getPermissionList();
                })
            },

            // 
            editPermission() {
                this.$refs.editFormRef.validate(async valid => {
                    if (!valid) return;
                    // 
                    const {data: result} = await this.$http.put(
                        'permission/' + this.editForm.id,
                        this.editForm
                    );

                    if (result.status !== 200) {
                        return this.$message.error(result.message)
                    }

                    // 
                    this.editDialogVisible = false;
                    // 
                    this.getPermissionList();
                    // 
                    this.$message.success(result.message)
                })
            },

            // , 
            async showEditDialog(id) {
                const {data: result} = await this.$http.get('permission/' + id);

                if (result.status !== 200) {
                    return this.$message.error(result.message)
                }

                this.editForm = result.data;
                this.editDialogVisible = true
            },
            // Id
            async removePermissionById(id) {
                // 
                const confirmResult = await this.$confirm(
                    ', ?',
                    '',
                    {
                        confirmButtonText: '',
                        cancelButtonText: '',
                        type: 'warning'
                    }
                ).catch(err => err);
                //  confirm
                //  cancel
                // console.log(confirmResult)
                if (confirmResult !== 'confirm') {
                    return this.$message.info('')
                }
                const {data: result} = await this.$http.delete('permission/' + id);

                if (result.status !== 200) {
                    return this.$message.error(result.message);
                }
                this.$message.success(result.message);
                this.getPermissionList()
            },

            // 
            addObjDialogClosed() {
                this.$refs.addFormRef.resetFields();
            },
            // 
            editObjDialogClosed() {
                this.$refs.editFormRef.resetFields();
            },
            //  pageSize 
            handleSizeChange(newSize) {
                console.log("pageSize: ", newSize);
                this.queryParams.pageSize = newSize;
                this.getPermissionList();
            },
            //   
            handleCurrentChange(newPage) {
                console.log("pageNum: ", newPage);
                this.queryParams.pageNum = newPage;
                this.getPermissionList();
            }

        },
        created() {
            this.getPermissionList();
        }

    }
</script>

<style lang="scss" scoped>
    @import "src/assets/sass/global";
    .el-card {
        .search-header {
            margin-bottom: 20px;
            ::v-deep .el-button--default:hover{
                background-color: $bg_blue_white_global;
            }
        }
        .el-pagination {
            margin-top: 20px;
        }
    }

</style>