<template>
    <div>
        <!--  -->
        <el-card>
            <!--  -->
            <el-row class="search-header" :gutter="10">
                <el-col :span="8">
                    <el-input placeholder="" v-model="queryParams.username" clearable @clear="getUserList">
                        <el-button slot="append" icon="el-icon-search" @click="getUserBySearch"></el-button>
                    </el-input>
                </el-col>
                <el-col :span="4">
                    <el-button type="primary" @click="addDialogVisible = true"></el-button>
                </el-col>
            </el-row>
            <!--  -->
            <el-table max-height="900px" :data="userList" border stripe>
                <el-table-column label="id"
                                 prop="id"
                                 width="80px"
                                 sortable></el-table-column>
                <el-table-column label="" prop="username"></el-table-column>
                <el-table-column label="" prop="mail"></el-table-column>
                <el-table-column label="" prop="phone"></el-table-column>
                <el-table-column label=""
                                 prop="role.name"
                                 :filters="userRoleFilters"
                                 :filter-method="filterRole"
                                 filter-placement="bottom-end"></el-table-column>
                <el-table-column label="" prop="createdDate"></el-table-column>
                <!--<el-table-column label="" prop="updatedDate"></el-table-column>-->
                <el-table-column label=""
                                 :filters="userStateFilters"
                                 :filter-method="filterState"
                                 filter-placement="bottom-end">
                    <template slot-scope="scope">
                        <el-switch
                                v-model="scope.row.state"
                                active-color="#36c6d3"
                                disabled>
                        </el-switch>
                    </template>
                </el-table-column>
                <el-table-column label="" width="200px">
                    <template slot-scope="scope">
                        <!--  -->
                        <el-button type="primary" icon="el-icon-edit" size="mini"
                                   @click="showEditDialog(scope.row.id)"></el-button>
                        <!--  -->
                        <el-button type="danger" icon="el-icon-delete" size="mini"
                                   @click="removeUserById(scope.row.id)"></el-button>
                        <!--  -->
                        <el-tooltip effect="dark" content="" placement="top" :enterable="false">
                            <el-button type="warning" icon="el-icon-setting" size="mini"
                                       @click="setRole(scope.row)"></el-button>
                        </el-tooltip>
                    </template>
                </el-table-column>
            </el-table>

            <!--  -->
            <el-pagination @size-change="handleSizeChange"
                           @current-change="handleCurrentChange"
                           :current-page="queryParams.pageNum"
                           :page-sizes="[1, 2, 3, 5, 10]"
                           :page-size="queryParams.pageSize"
                           :total="total"
                           layout="total, sizes, prev, pager, next, jumper">
            </el-pagination>

        </el-card>

        <!--  -->
        <el-dialog title="" :visible.sync="addDialogVisible" width="30%" @close="addUserDialogClosed">
            <!--  -->
            <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="80px">
                <el-form-item label="" prop="username">
                    <el-input v-model="addForm.username"></el-input>
                </el-form-item>
                <el-form-item label="" prop="password">
                    <el-input v-model="addForm.password"></el-input>
                </el-form-item>
                <el-form-item label="" prop="mail">
                    <el-input v-model="addForm.mail"></el-input>
                </el-form-item>
                <el-form-item label="" prop="phone">
                    <el-input v-model="addForm.phone"></el-input>
                </el-form-item>
            </el-form>
            <!--  -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="addDialogVisible = false"> </el-button>
                <el-button type="primary" @click="addUser"> </el-button>
            </span>
        </el-dialog>

        <!--  -->
        <el-dialog title="" :visible.sync="editDialogVisible" width="50%" @close="editUserDialogClosed">
            <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="70px">
                <el-form-item label="id">
                    <el-input v-model="editForm.id" disabled></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="editForm.username" disabled></el-input>
                </el-form-item>
                <el-form-item label="" prop="mail">
                    <el-input v-model="editForm.mail"></el-input>
                </el-form-item>
                <el-form-item label="" prop="phone">
                    <el-input v-model="editForm.phone"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                 <el-button @click="editDialogVisible = false"> </el-button>
                 <el-button type="primary" @click="editUser"> </el-button>
            </span>
        </el-dialog>

        <!--  -->
        <el-dialog title="" :visible.sync="setRoleDialogVisible" width="30%" @close="setRoleDialogClosed">
            <div style="text-align: left; display: inline-block;">
                <p>{{userInfo.username}}</p>
                <p>{{userInfo.role ? userInfo.role.name : null}}</p>
                <p>
                    <el-select v-model="selectedRoleId" placeholder="">
                        <el-option v-for="item in rolesList" :key="item.id" :label="item.name" :value="item.id">
                        </el-option>
                    </el-select>
                </p>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="setRoleDialogVisible = false"> </el-button>
                <el-button type="primary" @click="saveRoleInfo"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
    export default {
        name: "AdminUser",
        data() {
            // 
            const checkMail = (rule, value, callback) => {
                // 
                const regMail = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/;

                if (regMail.test(value)) {
                    // 
                    return callback()
                }
                callback(new Error(''))
            };
            // 
            const checkPhone = (rule, value, callback) => {
                // 
                const regPhone = /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/;
                if (regPhone.test(value)) {
                    return callback()
                }
                callback(new Error(''))
            };
            return {

                userRoleFilters: [
                    {
                        text: '',
                        value: '4'
                    },
                    {
                        text: 'VIP',
                        value: '3'
                    },
                    {
                        text: '',
                        value: '2'
                    },
                    {
                        text: '',
                        value: '1'
                    },
                ],
                userStateFilters: [
                    {
                        text: '',
                        value: '2'
                    },
                    {
                        text: '',
                        value: '1'
                    },
                ],

                // 
                queryParams: {
                    username: '',
                    // 
                    pageNum: 1,
                    // 
                    pageSize: 5
                },
                // 
                total: 0,
                // 
                userList: [],

                // 
                addDialogVisible: false,

                // 
                addForm: {
                    username: '',
                    password: '',
                    mail: '',
                    phone: ''
                },
                // 
                addFormRules: {
                    username: [
                        {required: true, message: '', trigger: 'blur'},
                        {
                            min: 3,
                            max: 20,
                            message: '3~20',
                            trigger: 'blur'
                        }
                    ],
                    password: [
                        {required: true, message: '', trigger: 'blur'},
                        {
                            min: 6,
                            max: 50,
                            message: '6~20',
                            trigger: 'blur'
                        }
                    ],
                    mail: [
                        {required: true, message: '', trigger: 'blur'},
                        {validator: checkMail, trigger: 'blur'}
                    ],
                    phone: [
                        {required: true, message: '', trigger: 'blur'},
                        {validator: checkPhone, trigger: 'blur'}
                    ]
                },

                // 
                editDialogVisible: false,
                // 
                editForm: {},
                // 
                editFormRules: {
                    mail: [
                        {required: true, message: '', trigger: 'blur'},
                        {validator: checkMail, trigger: 'blur'}
                    ],
                    phone: [
                        {required: true, message: '', trigger: 'blur'},
                        {validator: checkPhone, trigger: 'blur'}
                    ]
                },

                // 
                setRoleDialogVisible: false,
                // 
                userInfo: {},
                // 
                rolesList: [],
                // Id
                selectedRoleId: ''
            }
        },
        methods: {

            filterState(value, row) {
                return value === row.state;
            },
            filterRole(value, row) {
                return value === row.role;
            },

            // 
            async getUserList() {
                const {data: result} = await this.$http.get(
                    'user/_all',
                    {
                        params: {
                            pageNum: this.queryParams.pageNum,
                            pageSize: this.queryParams.pageSize
                        }
                    });
                if (result.status !== 200) {
                    return this.$message.error(result.message)
                }
                this.userList = result.data.content;
                console.log(result.data.content);
                this.total = result.data.totalElements;
            },

            // 
            async getUserBySearch() {
                if (this.queryParams.username === '') {
                    return this.userList.length > 1 ? this.$message.warning('') : this.getUserList();
                }
                const {data: result} = await this.$http.get(
                    'user/_username/' + this.queryParams.username,
                );
                if (result.status === 200) {
                    this.userList = [];
                    this.userList.push(result.data);
                    this.total = 1;
                } else {
                    this.userList = [];
                    this.total = 0;
                    return this.$message.error(result.message)
                }
            },

            // 
            addUser() {
                this.$refs.addFormRef.validate(async valid => {
                    if (!valid) return;
                    // 
                    console.log("admin  ======>");
                    console.log(this.addForm);
                    const {data: result} = await this.$http.post('register', this.addForm);

                    if (result.status !== 201) {
                        this.$message.error(result.message);
                    }
                    this.$message.success(result.message);
                    console.log("admin  ");
                    // 
                    this.addDialogVisible = false;
                    // 
                    this.getUserList();
                })
            },

            // 
            editUser() {
                this.$refs.editFormRef.validate(async valid => {
                    if (!valid) return;
                    // 
                    const {data: result} = await this.$http.put(
                        'user/' + this.editForm.id,
                        this.editForm
                    );

                    if (result.status !== 200) {
                        return this.$message.error(result.message)
                    }

                    // 
                    this.editDialogVisible = false;
                    // 
                    this.getUserList();
                    // 
                    this.$message.success(result.message)
                })
            },

            // , 
            async showEditDialog(id) {
                // console.log(id)
                const {data: result} = await this.$http.get('user/' + id);

                if (result.status !== 200) {
                    return this.$message.error(result.message)
                }

                this.editForm = result.data;
                this.editDialogVisible = true
            },

            // Id
            async removeUserById(id) {
                // 
                const confirmResult = await this.$confirm(
                    ', ?',
                    '',
                    {
                        confirmButtonText: '',
                        cancelButtonText: '',
                        type: 'warning'
                    }
                ).catch(err => err);
                //  confirm
                //  cancel
                // console.log(confirmResult)
                if (confirmResult !== 'confirm') {
                    return this.$message.info('')
                }
                const {data: result} = await this.$http.delete('user/' + id);

                if (result.status !== 200) {
                    return this.$message.error(result.message);
                }
                this.$message.success(result.message);
                this.getUserList()
            },

            // 
            async setRole(userInfo) {
                this.userInfo = userInfo;
                // 
                const {data: result} = await this.$http.get(
                    'role/_all',
                    {
                        params: {
                            pageNum: 1,
                            pageSize: 100
                        }
                    }
                );
                if (result.status !== 200) {
                    return this.$message.error(result.message);
                }
                this.rolesList = result.data.content;
                this.setRoleDialogVisible = true
            },

            // 
            async saveRoleInfo() {
                if (!this.selectedRoleId) {
                    return this.$message.error('')
                }

                const {data: result} = await this.$http.put(
                    `user/${this.userInfo.id}/role/${this.selectedRoleId}`
                );

                if (result.status !== 200) {
                    return this.$message.error(result.message);
                }
                this.getUserList();
                this.setRoleDialogVisible = false;
                this.$message.success(result.message);
            },

            //  switch 
            // async userStateChanged(userInfo) {
            //     const {data: res} = await this.$http.put(
            //         `users/${userInfo.id}/state/${userInfo.mg_state}`
            //     );
            //     if (res.meta.status !== 200) {
            //
            //         userInfo.mg_state = !userInfo.mg_state;
            //         return this.$message.error('');
            //     }
            //     this.$message.success('')
            // },

            // 
            addUserDialogClosed() {
                this.$refs.addFormRef.resetFields();
            },
            // 
            editUserDialogClosed() {
                this.$refs.editFormRef.resetFields();
            },
            // 
            setRoleDialogClosed() {
                this.selectedRoleId = '';
                this.userInfo = {}
            },
            //  pageSize 
            handleSizeChange(newSize) {
                console.log(newSize);
                this.queryParams.pageSize = newSize;
                this.getUserList();
            },
            //   
            handleCurrentChange(newPage) {
                console.log(newPage);
                this.queryParams.pageNum = newPage;
                this.getUserList();
            }
        },
        created() {
            this.getUserList();
        }
    }
</script>

<style lang="scss" scoped>
    @import "src/assets/sass/global";

    .el-card {
        .search-header {
            margin-bottom: 20px;

            ::v-deep .el-button--default:hover {
                background-color: $bg_blue_white_global;
            }
        }

        .el-pagination {
            margin-top: 20px;
        }
    }
</style>