<template>
    <div>
        <!--  -->
        <el-card>
            <!--  -->
            <el-row class="search-header" :gutter="20">
                <el-col :span="8">
                    <el-input placeholder="" v-model="queryParams.query" clearable @clear="getRoleList">
                        <el-button slot="append" icon="el-icon-search" @click="getRoleList"></el-button>
                    </el-input>
                </el-col>
                <el-col :span="4">
                    <el-button type="primary" @click="addDialogVisible = true"></el-button>
                </el-col>
            </el-row>
            <!--  -->
            <el-table :data="roleList" border stripe>
                <el-table-column type="expand">
                    <template slot-scope="props">
                        <el-tag class="permission-tag" closable @close="permissionClosed(permission.id,props.row.id)"
                                v-for="(permission,index) in props.row.permissionSet" :key="index">
                            {{permission.name}}
                        </el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="id" prop="id" sortable></el-table-column>
                <el-table-column label="name" prop="name"></el-table-column>
                <el-table-column label="" prop="description"></el-table-column>
                <el-table-column label="">
                    <template>
                        <el-switch
                                active-color="#36c6d3">
                        </el-switch>
                    </template>
                </el-table-column>
                <el-table-column label="" width="200px">
                    <template slot-scope="scope">
                        <!--  -->
                        <el-button type="primary" icon="el-icon-edit" size="mini"
                                   @click="showEditDialog(scope.row.id)"></el-button>
                        <!--  -->
                        <el-button type="danger" icon="el-icon-delete" size="mini"
                                   @click="removeRoleById(scope.row.id)"></el-button>
                        <!--  -->
                        <el-tooltip effect="dark" content="" placement="top" :enterable="false">
                            <el-button type="warning" icon="el-icon-setting" size="mini"
                                       @click="setPermission(scope.row)"></el-button>
                        </el-tooltip>
                    </template>
                </el-table-column>
            </el-table>

            <!--  -->
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
                           :current-page="queryParams.pageNum" :page-sizes="[1, 2, 5, 10]"
                           :page-size="queryParams.pageSize"
                           layout="total, sizes, prev, pager, next, jumper" :total="total">
            </el-pagination>

        </el-card>

        <!-- Role -->
        <el-dialog title="" :visible.sync="addDialogVisible" width="30%" @close="addRoleDialogClosed">
            <!--  -->
            <el-form :model="addForm" ref="addFormRef" label-width="80px">
                <el-form-item label="" prop="name">
                    <el-input v-model="addForm.name"></el-input>
                </el-form-item>
                <el-form-item label="" prop="description">
                    <el-input v-model="addForm.description"></el-input>
                </el-form-item>
            </el-form>
            <!--  -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="addDialogVisible = false"> </el-button>
                <el-button type="primary" @click="addRole"> </el-button>
            </span>
        </el-dialog>

        <!---->
        <el-dialog title="" :visible.sync="editDialogVisible" width="30%" @close="editObjDialogClosed">
            <el-form :model="editForm" ref="editFormRef" label-width="80">
                <el-form-item label="id">
                    <el-input v-model="editForm.id" disabled></el-input>
                </el-form-item>
                <el-form-item label="" prop="name">
                    <el-input v-model="editForm.name"></el-input>
                </el-form-item>
                <el-form-item label="" prop="description">
                    <el-input v-model="editForm.description"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                 <el-button @click="editDialogVisible = false"> </el-button>
                 <el-button type="primary" @click="editRole"> </el-button>
            </span>
        </el-dialog>

        <!--  -->
        <el-dialog title="" :visible.sync="setRoleDialogVisible" width="40%" @close="setRoleDialogVisible=false">
            <div>
                <el-transfer
                        style="text-align: left; display: inline-block;"
                        v-model="value"
                        :props="{key: 'id',label: 'name'}"
                        :titles="['', '']"
                        :data="permissionList">
                </el-transfer>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="setRoleDialogVisible = false"> </el-button>
                <el-button type="primary" @click="savePermissionInfo"> </el-button>
            </span>
        </el-dialog>


    </div>
</template>

<script>
    export default {
        name: "AdminRole",
        data() {
            return {
                value: [],
                // 
                queryParams: {
                    query: '',
                    // 
                    pageNum: 1,
                    // 
                    pageSize: 2
                },
                // 
                total: 0,
                // 
                roleList: [],
                roleId: undefined,
                permissionList: [],

                // 
                addDialogVisible: false,
                // 
                addForm: {
                    name: '',
                    description: '',
                    permissionSet: []
                },
                // 
                editDialogVisible: false,
                // 
                setRoleDialogVisible: false,
                // 
                editForm: {},

            };
        },
        methods: {
            async getPermissionList() {
                const {data: result} = await this.$http.get(
                    'permission/_all',
                    {
                        params: {
                            pageNum: 1,
                            pageSize: 100
                        }
                    }
                );

                if (result.status === 200) {
                    this.permissionList = result.data.content;
                } else {
                    this.$message.error("");
                }
            },
            // 
            async savePermissionInfo() {
                const {data: result} = await this.$http.put(
                    'role/' + this.roleId + '/permission',
                    {
                        permissionList: this.value
                    }
                );
                if (result.status === 200) {
                    this.$message.success(result.message);
                    this.getRoleList();
                } else {
                    this.$message.error(result.message);
                }
            },
            // 
            async permissionClosed(permissionId, roleId) {
                console.log(permissionId, roleId);
                const {data: result} = await this.$http.delete(
                    'role/' + roleId + '/permission/' + permissionId
                );
                if (result.status === 200) {
                    this.$message.success(result.message);
                    this.getRoleList();
                } else {
                    this.$message.error(result.message);
                }
            },

            // 
            setPermission(row) {
                for (let i = 0; i < row.permissionSet.length; i++) {
                    this.value.push(row.permissionSet[i].id);
                }
                this.roleId = row.id;
                this.setRoleDialogVisible = true;
            },

            // 
            async getRoleList() {
                const {data: result} = await this.$http.get(
                    'role/_all',
                    {
                        params: {
                            pageNum: this.queryParams.pageNum,
                            pageSize: this.queryParams.pageSize
                        }
                    });
                if (result.status !== 200) {
                    return this.$message.error(result.message)
                }
                this.roleList = result.data.content;
                this.total = result.data.totalElements;
            },

            // 
            async getPermissionByName() {
                if (this.queryParams.query === '') {
                    return this.permissionList.length > 1 ? this.$message.warning('') : this.getPermissionList();
                }
                const {data: result} = await this.$http.get(
                    'movie/_name/' + this.queryParams.query,
                );
                if (result.status === 200) {
                    this.movieList = [];
                    this.movieList.push(result.data);
                    this.total = 1;
                } else {
                    this.movieList = [];
                    this.total = 0;
                    return this.$message.error(result.message)
                }
            },

            // 
            addRole() {
                this.$refs.addFormRef.validate(async valid => {
                    if (!valid) return;
                    // 
                    console.log("admin  ======>");
                    const {data: result} = await this.$http.post('role', this.addForm);

                    if (result.status !== 201) {
                        this.$message.error(result.message);
                    }
                    this.$message.success(result.message);
                    console.log("admin  ");
                    // 
                    this.addDialogVisible = false;
                    // 
                    this.getRoleList();
                })
            },

            // 
            editRole() {
                this.$refs.editFormRef.validate(async valid => {
                    if (!valid) return;
                    // 
                    const {data: result} = await this.$http.put(
                        'role/' + this.editForm.id,
                        this.editForm
                    );

                    if (result.status !== 200) {
                        return this.$message.error(result.message)
                    }

                    // 
                    this.editDialogVisible = false;
                    // 
                    this.getRoleList();
                    // 
                    this.$message.success(result.message)
                })
            },

            // , 
            async showEditDialog(id) {
                console.log(id);
                const {data: result} = await this.$http.get('role/' + id);

                if (result.status !== 200) {
                    return this.$message.error(result.message)
                }

                this.editForm = result.data;
                this.editDialogVisible = true
            },
            // Id
            async removeRoleById(id) {
                // 
                const confirmResult = await this.$confirm(
                    ', ?',
                    '',
                    {
                        confirmButtonText: '',
                        cancelButtonText: '',
                        type: 'warning'
                    }
                ).catch(err => err);
                //  confirm
                //  cancel
                // console.log(confirmResult)
                if (confirmResult !== 'confirm') {
                    return this.$message.info('')
                }
                const {data: result} = await this.$http.delete(`role/${id}`);

                if (result.status !== 200) {
                    return this.$message.error(result.message);
                }
                this.$message.success(result.message);
                this.getRoleList();
            },

            // 
            addRoleDialogClosed() {
                this.$refs.addFormRef.resetFields();
            },

            // 
            editObjDialogClosed() {
                this.$refs.editFormRef.resetFields();
            },
            //  pageSize 
            handleSizeChange(newSize) {
                console.log("pageSize: ", newSize);
                this.queryParams.pageSize = newSize;
                this.getRoleList();
            },
            //   
            handleCurrentChange(newPage) {
                console.log("pageNum: ", newPage);
                this.queryParams.pageNum = newPage;
                this.getRoleList();
            }


        },
        created() {
            this.getRoleList();
            this.getPermissionList();
        }

    }
</script>

<style lang="scss" scoped>
    @import "src/assets/sass/global";

    .el-card {
        .search-header {
            margin-bottom: 20px;

            ::v-deep .el-button--default:hover {
                background-color: $bg_blue_white_global;
            }
        }

        .el-pagination {
            margin-top: 20px;
        }

        .permission-tag {
            margin: 10px;
        }
    }
    ::v-deep .el-checkbox-group {
        width: 200px;
    }

</style>